import { encrypt, decrypt } from "./SHA256Hasher";

const MODEL = {
  user: 'user',
  darkMode: 'darkMode',
  profileComplete: 'ProfileComp',
  userData: 'userData',
  appData: 'appData',
  notification: 'notification',
  portfolio: 'portfolio',
  mintbox: 'mintbox',
  fcmToken: 'fcmToken'
};

const db = {
  // hash : new SHA256Hasher(),
  model: MODEL,
  get: async (key) => {
    key = MODEL[key] ? MODEL[key] : key;
    const data = await localStorage.getItem(key);
    return data;
  },
  set: async (key, val) => {
    const data = await localStorage.setItem(key, val);
    return data;
  },
  remove: async (key) => await localStorage.removeItem(key),
  clear: async () => {
    await localStorage.removeItem('theme');
    await localStorage.multiRemove(Object.values(MODEL));
  },
};

export default db;