import React, { useState } from 'react'
import './toggle.css'

export default function ToggleButton({ checked=false }) {
  const [isChecked, setIsChecked] = useState(checked);
  const handleChange = () => setIsChecked(s => !s);
  return (
    <div className="toggle-container">
      <label>
        <input className="toggle" checked={isChecked} onChange={handleChange} 
          type="checkbox" />
        <div>
          <div></div>
        </div>
      </label>
    </div>
  )
}

